export default "import { Button, IconButton } from '@material-ui/core';\nimport { icons } from './Icons';\nimport React from 'react';\nimport styled from 'styled-components';\n\nexport default () => {\n  return (\n    <>\n      <Grid>\n        {icons.map((icon, i) => {\n          return (\n            <Button key={'button' + i} variant=\"contained\" color=\"primary\" startIcon={icon}>\n              Button\n            </Button>\n          );\n        })}\n      </Grid>\n\n      <Grid>\n        {icons.map((icon, i) => {\n          return (\n            <IconButton key={'icon-button' + i} color=\"primary\">\n              {icon}\n            </IconButton>\n          );\n        })}\n      </Grid>\n    </>\n  );\n};\n\nconst Grid = styled.div`\n  display: grid;\n  grid-template-columns: repeat(5, 150px);\n  grid-auto-rows: 50px;\n  grid-gap: 10px;\n  justify-items: center;\n  align-items: center;\n  margin-bottom: 20px;\n`;\n"