export default "import {\n  AdjustIcon,\n  CircleIcon,\n  FlipIcon,\n  InvertIcon,\n  LunitIcon,\n  MagnifyIcon,\n  PanIcon,\n  PenIcon,\n  ResetIcon,\n} from '@lunit/opt-control-icons';\nimport React from 'react';\nimport styled from 'styled-components';\n\nexport const icons = [\n  <AdjustIcon />,\n  <FlipIcon />,\n  <InvertIcon />,\n  <MagnifyIcon />,\n  <PanIcon />,\n  <PenIcon />,\n  <ResetIcon />,\n  <CircleIcon />,\n  <LunitIcon />,\n];\n\nexport default () => {\n  return (\n    <Grid>\n      {icons.map((icon, i) => {\n        return <div key={'icon' + i}>{icon}</div>;\n      })}\n    </Grid>\n  );\n};\n\nconst Grid = styled.div`\n  display: grid;\n  grid-template-columns: repeat(5, 50px);\n  grid-auto-rows: 50px;\n  grid-gap: 10px;\n  justify-items: stretch;\n  align-items: stretch;\n\n  > div {\n    background-color: silver;\n    color: black;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n  }\n`;\n"